import React, { Component } from "react";
import Stage from "./form-stages/stage";
import helper from "./helper";

class Form extends Component {
  state = {
    fields: {
      fruit: { apple: false, orange: false, banana: false },
    },
    required: [
      //true means validated , false means invalid
      { name: false, lastName: false, phone: false },
      { email: false, gender: false },
    ],
    errors: {},
    currentPage: 1,
    MaxPageNumber: 3,
    items: [],
    item: "",
    buttonError: "",
  };

  handleChange = (e) => {
    let fields = { ...this.state.fields };
    let required = this.state.required[this.state.currentPage - 1];
    if (e.target.type === "checkbox") {
      fields[e.target.name][e.target.value] = !(e.target.dataset.check ===
      "false"
        ? false
        : true);
    } else {
      let validation = helper(e.target.value, e.target.type);
      if (typeof validation === "boolean") {
        this.state.errors[e.target.name] = "";
        required[e.target.name] = true;
      } else {
        this.state.errors[e.target.name] = validation;
        required[e.target.name] = false;
      }
      fields[e.target.name] = e.target.value;
    }
    //check for errors
    let valid = true;
    Object.keys(required).forEach((field) => {
      if (!required[field]) valid = false;
    });
    if (valid) {
      this.setState({ buttonError: "" });
    }
    this.setState({ fields });
  };
  handleNext = (e) => {
    e.preventDefault();

    let required = this.state.required[this.state.currentPage - 1];
    let valid = true;
    Object.keys(required).forEach((field) => {
      if (!required[field]) valid = false;
    });
    if (valid) {
      let page = this.state.currentPage;
      page++;

      this.setState({
        currentPage: page,
        buttonError: "",
      });
    } else {
      this.setState({ buttonError: "Please fill the required field" });
    }
  };
  handlePre = (e) => {
    e.preventDefault();
    let page = this.state.currentPage;
    page--;
    this.setState({ currentPage: page, buttonError: "" });
  };
  handleItemOnChange = (e) => {
    let item = e.target.value;
    this.setState({ item });
  };
  handleAddItem = (e) => {
    e.preventDefault();
    let items = [...this.state.items];
    let item = this.state.item;
    if (item === "") return;
    items.push(item);
    item = "";
    this.setState({ items, item });
  };
  handleItemRemove = (e) => {
    let itemValue = e.target.dataset.value;
    let items = this.state.items;
    if (items.indexOf(itemValue) !== -1)
      items = items.filter((item) => item !== itemValue);
    this.setState({ items });
  };
  render() {
    const {
      currentPage,
      MaxPageNumber,
      errors,
      fields,
      item,
      items,
    } = this.state;
    return (
      <div className="container">
        <form style={this.state.style}>
          <Stage
            page={currentPage}
            handleChange={this.handleChange}
            errors={errors}
            fields={fields}
            handleItemOnChange={this.handleItemOnChange}
            handleAddItem={this.handleAddItem}
            item={item}
            items={items}
            handleItemRemove={this.handleItemRemove}
          />

          {currentPage > 1 ? (
            <button onClick={this.handlePre}>Previous</button>
          ) : (
            ""
          )}
          {currentPage !== MaxPageNumber ? (
            <button onClick={this.handleNext}>Next</button>
          ) : (
            <input type="submit" value="submit" />
          )}

          <span className="btnError">{this.state.buttonError}</span>
        </form>
      </div>
    );
  }
}

export default Form;
