function helper(value, type) {
  switch (type) {
    case "text":
      return value.length > 0 || "*can not be empty!";
      break;
    case "email":
      const regex = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/;
      return regex.test(value) || "*invalid email address";
      break;
    case "tel":
      const regexPhone = /\d{10}/;
      return regexPhone.test(value) || "*invalid phone number";
      break;
    case "radio":
      return true || "please choose at least one";
      break;
  }
}

export default helper;
